// hooks/useApplicationData.ts
'use client';

import { useState } from 'react';
import api from '@/lib/api';

export const useApplicationData = () => {
  const [isLoading, setIsLoading] = useState(false);

  const getCurrentApplicationData = async (applicationId: string) => {
    try {
      setIsLoading(true);
      const response = await api.get(`/v1/Applications/GetById?ApplicationId=${applicationId}`);
      
      if (response.data.code === 200 && response.data.data) {
        return response.data.data;
      }
      return null;
    } catch (error) {
      console.error('Erro ao buscar dados da aplicação:', error);
      return null;
    } finally {
      setIsLoading(false);
    }
  };

  return {
    isLoading,
    getCurrentApplicationData,
  };
};